.projectpage {
	padding-bottom: 50px;
}

.container {
	width: 90%;
	margin: 0 auto;
	padding: 0;
}

.left {
	float: left;
	width: 65%;
	padding-right: 40px;
	padding-bottom: 40px;
}

.h3, .h4 {
	text-align: left;
	padding: 0;
}

.right {
	float: right;
	width: 25%;
	padding-bottom: 30px;
}

.right p {
	margin: 10px 0;
}

.imgmobile {
	display: none;
}

@media (max-width: 700px) {
	
	.projectpage {
	padding-bottom: 20px;
	}

	.left, .right {
		display: block;
		width: 100%;
	}

	.left {
		padding: 0;
	}

	.right {
		padding-bottom: 40px;
	}

	.imgmobile {
		display: block;
		margin-bottom: 20px;
	}
}